(function() {
  'use strict';

  angular
    .module('springbootangularhtml5', ['ngRoute']);

})();

(function () {
    'use strict';

    angular
        .module('springbootangularhtml5')
        .controller('MainController', MainController);

    /** @ngInject */
    function MainController($http, $log) {
        var vm = this;

        vm.callService = function () {
            $http.get('api/service1').then(function (response) {
                vm.result = response;
            }, function (reason) {
                $log.error(reason);
            });
        };
    }
})();

(function() {
  'use strict';

  angular
    .module('springbootangularhtml5')
    .run(runBlock);

  /** @ngInject */
  function runBlock($log) {

    $log.debug('runBlock end');
  }

})();

(function() {
  'use strict';

  angular
    .module('springbootangularhtml5')
    .config(routeConfig);

  function routeConfig($routeProvider, $locationProvider) {
    $routeProvider
      .when('/home', {
        templateUrl: 'app/main/main.html',
        controller: 'MainController',
        controllerAs: 'vm'
      })

        .when('/home/iam/a/very/long/url', {
            templateUrl: 'app/main/second.html',
            controller: 'MainController',
            controllerAs: 'vm'
        })
      .otherwise({
        redirectTo: '/home'
      });

      $locationProvider.html5Mode(true);
  }

})();

angular.module("springbootangularhtml5").run(["$templateCache", function($templateCache) {$templateCache.put("app/main/main.html","<a href=home/iam/a/very/long/url>I am a very long fragmented uri - click me</a><br>Spring will forward the requested url to index.html, and angular will do the job.");
$templateCache.put("app/main/second.html","\\o/ Now you can refresh me (hit F5)<br><button ng-click=vm.callService()>Click me to call backend</button><br><pre>\n{{vm.result | json}}\n</pre>");}]);